.widget-grid {
    display: grid;
    grid-auto-rows: 0; // Disable implicit rows
    grid-auto-columns: 0; // Disable implicit columns
    grid-template-rows: repeat(6, minmax(160px, 1fr));
    grid-template-columns: 1fr;
    row-gap: 10px;

    @include pnp-media-breakpoint-up(md) {
        grid-template-rows: repeat(6, minmax(160px, 1fr));
        grid-template-columns: repeat(2, 1fr);
        column-gap: 10px;
    }

    @include pnp-media-breakpoint-up(xl) {
        grid-template-rows: repeat(6, minmax(160px, 1fr));
        grid-template-columns: repeat(3, 1fr);
    }
}

.widget-grid__container {
    @include pnp-media-breakpoint-up(md) {
        @for $i from 1 to 3 {
            &--startColumnTablet-#{$i} {
                grid-column-start: #{$i};
            }
        }

        @for $i from 1 to 3 {
            &--endColumnTablet-#{$i} {
                grid-column-end: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--startRowTablet-#{$i} {
                grid-row-start: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--endRowTablet-#{$i} {
                grid-row-end: #{$i};
            }
        }
    }

    @include pnp-media-breakpoint-up(xl) {
        @for $i from 1 to 4 {
            &--startColumnDesktop-#{$i} {
                grid-column-start: #{$i};
            }
        }

        @for $i from 1 to 4 {
            &--endColumnDesktop-#{$i} {
                grid-column-end: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--startRowDesktop-#{$i} {
                grid-row-start: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--endRowDesktop-#{$i} {
                grid-row-end: #{$i};
            }
        }
    }
}
