.card {
    @include pnp-typeface(text-14, no-margin);
    background-color: $white;
    border: 1px solid $edify-color-light;

    .header {
        @include pnp-reset-heading();
        @include pnp-typeface(text-14-bold, no-margin);
        @include pnp-svg-icon-classes();
        display: flex;

        align-items: center;
        justify-content: space-between;
        min-height: 20px;
        padding: 10px;
        color: $edify-color-dark;
    }

    .header-right {
        display: flex;
        align-items: inherit;
    }

    .header-text {
        @include pnp-reset-heading();
        margin-right: $edify-card-padding;
        color: $edify-color-dark;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .sub-title {
        margin-right: 20px;
    }

    .more-menu-button {
        @include pnp-reset-button();

        padding: $edify-card-padding;
        color: $edify-color-dark;
        transform: rotate(-90deg);

        &:hover,
        &:focus {
            background-color: rgba(0, 0, 0, 0.04);
        }

        .collapsed {
            transform: rotate(180deg);
        }
    }

    .contents {
        height: calc(100% - 44px);
        padding: $edify-card-padding;
        border-top: 1px solid $edify-color-light;
    }

    .scrollable {
        overflow: auto;
    }
}
