@import '../../edify-card/css/global';
@import '../../circular-progress/css/global';

.analytics {
    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__progress-circle--grey {
        .circle {
            fill: $edify-color-light;
        }
    }

    &__progress-details {
        @include pnp-typeface(text-14, no-margin);

        flex: 1;
        align-content: baseline;
        margin: 0 0 8px 16px;
        color: $edify-color-dark;

        h4 {
            @include pnp-reset-heading;
            @include pnp-typeface(text-14-bold, no-margin);
        }
    }
}

.circular-progress-circle__chart--progress {
    stroke: $edify-color-yellow;
}

.circular-progress-circle__description {
    @include pnp-typeface(heading-26-bold, no-margin);
}
